* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 10px;
}

body {
  font-family: "Montserrat", sans-serif;
}

.container {
  max-width: 1320px;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 10rem;
  background-color: #000;
  opacity: 0.9;
}
.header .header-box {
  display: flex;
  justify-content: space-between;
  padding: 0 2rem;
}
.header .header-box .header__logo img {
  height: 10rem;
}
.header .header-box .header__menu {
  display: flex;
  font-size: 1.6rem;
}
@media screen and (max-width: 1000px) {
  .header .header-box .header__menu {
    position: absolute;
    top: 10rem;
    left: 0;
    flex-direction: column;
    align-items: center;
    width: 100%;
    background-color: #000;
  }
}
.header .header-box .header__menu li {
  list-style-type: none;
}
.header .header-box .header__menu li a {
  display: flex;
  align-items: center;
  height: 10rem;
  position: relative;
  font-weight: bold;
  margin: 0 3rem;
  color: #fff;
  text-decoration: none;
  letter-spacing: 0.3rem;
}
@media screen and (max-width: 1000px) {
  .header .header-box .header__menu li a {
    width: 100%;
    height: 5rem;
    margin: 0;
    background: mediumpurple;
  }
}
.header .header-box .header__menu li a:after, .header .header-box .header__menu li a:before {
  content: "";
  position: absolute;
  width: 0;
  height: 0.1rem;
  transition: all 0.3s ease-in;
  margin-top: 0.2rem;
}
.header .header-box .header__menu li a:after {
  bottom: 25%;
  left: 0;
}
.header .header-box .header__menu li a:before {
  top: 25%;
  right: 0;
}
.header .header-box .header__menu li a:hover:after {
  width: 100%;
  height: 0.1rem;
  bottom: 25%;
  background: #fff;
}
.header .header-box .header__menu li a:hover:before {
  width: 100%;
  height: 0.1rem;
  top: 25%;
  background: #fff;
}
.header .header-box .material-icons.hamburger {
  display: none;
}
@media screen and (max-width: 1000px) {
  .header .header-box .material-icons.hamburger {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 4rem;
    color: #fff;
    cursor: pointer;
    transition: 0.2s ease-in;
  }
  .header .header-box .material-icons.hamburger:hover {
    transform: scale(1.3);
  }
}

body {
  height: 200vh;
  background: peru;
}